import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import test from '@/components/test'
import Index from '@/components/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      
      component: Index
    },
    {
      path: '/hello',
      
      component: Hello
    },
    {
      path: '/test',
     
      component: test
    }
  ]
})



